import Materialize from "materialize-css";

export default {
  custom: {
    info: (message, length) => {
      Materialize.toast({
        html: message,
        displayLength: length,
        classes: "rounded info-toast"
      });
    }
  },
  mail: {
    resetPassword: () => {
      Materialize.toast({
        html: "Te enviamos un Email para que puedas generar una nueva contrasea",
        displayLength: 3000,
        classes: "rounded info-toast"
      });
    }
  },
  auth: {
    changedPassword: () => {
      Materialize.toast({
        html: "Tu contrasea ha cambiado",
        displayLength: 3000,
        classes: "rounded info-toast"
      });
    }
  }
};
